.card {
  background: #fff;
  border-radius: 2px;
  height: 200px;
  margin: 1rem;
  position: relative;
  width: 325px;
  box-shadow: 0 3px 6px #999, 0 3px 6px #999;
  text-align: left;
}

.treeImage {
  width:100%;
}

.card > .img-container {
  height: 100%;
  overflow: hidden;
  text-align: center;
  background:#6CADDC;
}
.card > .img-container > img {
  /* width: 60% */
}

.card > .content {
  padding-left: 1rem;
  padding-right: 1rem;
  font-size: 15px;
}

.card > .content > ul {
  list-style-type: none;
}

.card > .content > ul > li {
  margin-top: 18px;
}

.card > .remove {
  color: #ff1744;
  font-size: 48px;
  position: absolute;
  top: -20px;
  right: 5px;
  cursor: pointer;
}

.card > .remove:hover {
  color: #d50000;
}

.wrapper {
  padding-top: 10px;
  /* background: #ECEFF1; */
  /* height: 100%; */
  display: flex;
  flex-flow: row wrap;
  padding: 20px;
  justify-content: space-around;
  align-content: flex-start;
  overflow: auto;
}

.title {
  width: 100%;
  text-align: center;
  font-size: 5rem;
  font-family: 'Cabin Sketch', cursive;
  margin-top: 0px;
  margin-bottom:0px;
  overflow: hidden;
  background-color: fff;
  position: fixed;
  top: 0;
  z-index: 1;
  background-color: rgb(25, 143, 4);
  padding-bottom: 15px;
}

.score{
  font-size: 30px;
  margin-bottom: 0px;
  margin-top: 0px;
}


.title {
  width: 100%;
  text-align: center;
  margin-bottom: 0px;
}

* {
  box-sizing: border-box;
  font-family: sans-serif;
}

html, body, #root {
  padding: 0;
  margin-bottom: 20px;
  height: 100%;
  margin-top: 100px;
}
